@import "src/common/styles/sass/Variables";
@import "src/common/styles/sass/Mixins";

.btn {
  border: none;
  @include flex(row, center, center, nowrap);
  @include text($textFontFamily, 14px, 1.2, 700);
  text-transform: uppercase;
  letter-spacing: 1px;
  padding: 8px 20px;
  max-width: 160px;
  width: 100%;
  color: $textColor;
  outline: none;
  position: relative;
  z-index: 0;

  &::after {
    content: "";
    position: absolute;
    width: 70%;
    height: 10px;
    background: $primaryColor;
    bottom: 0;
    z-index: -1;
    transition: all 0.5s ease-in-out;
  }

  &:hover {
    text-decoration: none;
    color: $textColor;

    &::after {
      width: 100%;
      height: 110%;
      transition: all 0.7s ease-in-out 0s;
    }
  }
}